/* Typography */
html {
  font-size: 10px;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Avenir", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #4c4c4d;
  line-height: 1.42857;
  background-color: #fff;
}

h1, h2, h3, h4, h5, h6 {
  font-family: inherit;
  font-weight: 900;
  line-height: 1.1;
  margin: 0 0 1.5rem 0;
}

h1, .h1 {
  font-size: 3.6rem;
}

h2, .h2 {
  font-size: 3rem;
}

h3, .h3 {
  font-size: 2.4rem;
}

h4, .h4 {
  font-size: 1.8rem;
}

h5, .h5 {
  font-size: 1.4rem;
}

h6, .h6 {
  font-size: 1.2rem;
}

/* Custom Grids
* ===============
*
* Place all your custom grid declarations here */
.flex-row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -10px;
  margin-left: -10px;
}

.block {
  padding-right: 10px;
  padding-left: 10px;
  min-width: 0;
  text-align: center;
}

.block-1-5 {
  flex: 0 1 20%;
}

.block-1-3 {
  flex: 0 1 33.33%;
}

.block-1-4 {
  flex: 0 1 25%;
}

@media only screen and (max-width: 950px) {
  .flex-row {
    justify-content: center;
  }
  .block-1-5 {
    flex: 0 1 33.33%;
  }
}

@media only screen and (max-width: 600px) {
  .block-1-5,
  .block-1-4 {
    flex: 0 1 50%;
  }
  .block-1-3 {
    flex: 0 1 100%;
  }
}

