*
* Place all your helpers declarations here */
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  clear: both;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.text-center {
  text-align: center;
}

.text-justify {
  text-align: justify;
}

.error {
  border: 3px solid #D2232A !important;
}

.screen-reader-text {
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important;
}

/* Buttons
* ===============
*
* Place all your buttons declarations here */
.btn-theme {
  display: inline-block;
  line-height: 1;
  border: 1px solid #f47820;
  border-radius: 0;
  background: #f47820;
  color: #ffffff;
  font-weight: 500;
  font-family: "Avenir", sans-serif;
  font-size: 20px;
  padding: .75rem 1.25rem;
  margin: 0;
  white-space: nowrap;
  vertical-align: middle;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  position: relative;
  overflow: hidden;
  transition: all 0.3s;
}

.btn-theme:hover {
  background-color: #d65f0b;
  border-color: #d65f0b;
}

